<template>
  <v-container fluid pa-0>
    <v-col
      class="pa-0"
      align="center" 
      justify="center"
      wrap
    >
      <!-- <v-carousel :height="imgHeight" cycle reverse hide-delimiter-background show-arrows-on-hover>
        <v-carousel-item 
          v-for="(item,i) in mainItems" 
          :key="i"
          :src="require(`@/assets/portfolio/${item.main_img}`)" 
          transition="slide"
        >
        <v-col cols="12" lg="11" align="start" class="main_item" :class="item.color">
          <p class="main_date">{{item.date}}</p>
          <p class="main_title">{{item.title}}</p>
          <p class="main_sub">{{item.sub}}</p>
          <img :src="require(`@/assets/portfolio/${item.icon}`)" class="icon_img">
        </v-col>
        </v-carousel-item>
      </v-carousel> -->
      
      <v-col cols="12" lg="10" align="start" class="profile main_item">
        <p class="main_title">UI/UX Desgin</p>
        <p class="main_title pb-7">Front-End Developer</p>
        <p class="main_sub">다양한 포지션 경험 및 실무능력 수행 가능한 프로그램 기획 / 디자인 & Front-End 개발자</p>
        <p class="main_sub">Sungchan Cho - <span class="contact">chok0908@naver.com</span></p>
      </v-col>
      <v-col class="col-lg-10">
        <div v-if="small == true">
          <v-col cols="12" sm="11" class="main_card_item">
            <v-row align="center" justify="center">
              <v-col cols="6" sm="6" md="6" class="pa-4 img1_box" v-on:click="link(mainItem1.index)">
                <v-img
                  class="img1"
                  :src="require(`@/assets/portfolio/${mainItem1.main_img}`)"
                >
                </v-img>
                <v-img
                  class="shadow"
                  :src="require(`@/assets/portfolio/${mainItem1.main_shadow}`)"
                >
                </v-img>
              </v-col>
              <v-col cols="12" sm="6" md="6" class="main_text_group">
                <v-card-text class="main_item_title">{{mainItem1.title}}</v-card-text>
                <v-card-subtitle class="main_item_text">{{mainItem1.text}}</v-card-subtitle>
                <v-col align="end" class="pa-0">
                  <v-card-text class="main_item_text text--primary ">{{mainItem1.date}}</v-card-text>
                </v-col>
                <v-col align="end" justify="center">
                  <v-btn v-on:click="link(mainItem1.index)" text large class="main_card view_more_btn">VIEW MORE</v-btn>
                </v-col>
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="12" sm="11" class="main_card_item">
            <v-row align="center" justify="center">
              <v-col cols="12" sm="6" md="6" class="main_text_group_right">
                <v-card-text class="main_item_title">{{mainItem2.title}}</v-card-text>
                <v-card-subtitle class="main_item_text">{{mainItem2.text}}</v-card-subtitle>
                <v-col align="end" class="pa-0">
                  <v-card-text class="main_item_text text--primary ">{{mainItem2.date}}</v-card-text>
                </v-col>
                <v-col align="end">
                  <v-btn v-on:click="link(mainItem2.index)" text large class="main_card view_more_btn">VIEW MORE</v-btn>
                </v-col>
              </v-col>
              <v-col cols="12" sm="6" md="6" class="pa-3" v-on:click="link(mainItem2.index)">
                <v-img
                  class="img2"
                  :src="require(`@/assets/portfolio/${mainItem2.main_img}`)"
                >
                </v-img>
              </v-col>
            </v-row>
          </v-col>
          <v-col cols="12" sm="11" class="main_card_item">
            <v-row align="center" justify="center">
              <v-col cols="12" sm="6" md="6" class="pa-3" v-on:click="link(mainItem3.index)">
                <v-img
                  class="img3"
                  :src="require(`@/assets/portfolio/${mainItem3.main_img}`)"
                >
                </v-img>
              </v-col>
              <v-col cols="12" sm="6" md="6" class="main_text_group">
                <v-card-text class="main_item_title">{{mainItem3.title}}</v-card-text>
                <v-card-subtitle class="main_item_text">{{mainItem3.text}}</v-card-subtitle>
                <v-col align="end" class="pa-0">
                  <v-card-text class="main_item_text text--primary ">{{mainItem3.date}}</v-card-text>
                </v-col>
                <v-col align="end">
                  <v-btn v-on:click="link(mainItem3.index)" text class="main_card view_more_btn">VIEW MORE</v-btn>
                </v-col>
              </v-col>
            </v-row>
          </v-col>
        </div>
        <v-row v-if="small == false">
          <v-col cols="12" sm="12" md="4" class="main_card_item" v-for="(item1,i) in mainItems" :key="i">
            <v-card
              class="mx-auto main_card img_box"
              max-width="100%"
              outlined
              v-on:click="link(i)"
            >
            <v-row class="ma-0">
              <v-col cols="6" sm="6" md="12" class="pa-0">
                <v-img
                  class="grey--text align-end main_item_img"
                  :src="require(`@/assets/portfolio/${item1.sub_img}`)"
                >
                </v-img>
              </v-col>
              <v-col cols="6" sm="6" md="12" align="start" justify="center" class="pa-3">
                <v-card-text class="main_item_title">{{item1.title}}</v-card-text>
                <v-card-subtitle class="main_item_text">{{item1.text}}</v-card-subtitle>
                <v-col align="end" class="pa-0">
                  <v-card-text class="main_item_text text--primary ">{{item1.date}}</v-card-text>
                </v-col>
              </v-col>
            </v-row>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-col>
  </v-container>
</template>

<script>
import $ from "jquery";
export default {
  data: () => ({
    imgHeight: 700,
    smallWidth: false,
    small: false,
    mainItem1:[],
    mainItem2:[],
    mainItem3:[]
  }),
  computed:{
    mainItems(){
      return this.$store.getters.getMain;
    }
  },
  created(){
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.itemDivide();
  },
  mounted(){
    this.$store.commit('changeTitle','main project');
    if(this.smallWidth == true){
      this.$eventBus.$emit('drawerHome', false);
    }
  },
  methods:{
    itemDivide(){
      this.mainItem1 = this.mainItems[0];
      this.mainItem2 = this.mainItems[1];
      this.mainItem3 = this.mainItems[2];
    },
    handleResize(){
      var width = window.innerWidth;
      // width = 1264 이하 일때 동작하지 않음
      if( width > 1264 ){
        this.smallWidth = true;
        this.imgHeight = 700;
      }if( width < 768 ){
        this.imgHeight = 600;
      }if( width < 600 ){
        this.smallWidth = false;
        this.imgHeight = 500;
      }
      if( width < 600 ){
        this.small = false;
      }else{
        this.small = true;
      }
    },
    link(value){
      this.$store.dispatch('changeMain',value);
      this.$router.push('/mainContents').catch(err => err);
    }
  },
  updated(){
  },
  watch:{
  },
  beforeDestroy(){
    if(this.smallWidth == true){
      this.$eventBus.$emit('drawerHome', true);
    }
    window.removeEventListener('resize', this.handleResize)
  }
}
</script>

<style>
/* play with some lines below */
#progress-container-el {
  /* background */
  background-color: transparent !important;
  top: calc(100% - 4px) !important;
}
#progress-el {
  /* progress bar */
  background-color: red !important;
}
.contact{
  font-weight: 600;
  color: #2567C0 !important;
}
.light-text{
  color: #fff !important;
}
.dark-text{
  color: #192E49 !important;
}
.profile{
  /* background-color: #ddd; */
  height: 380px;
}
.main_item{
  padding: 5rem;
  text-align: left;
}
.view_more_btn{
  color: #2567C0 !important;
}
.main_title{
  font-size: 2.3rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.4rem;
}
.main_sub{
  font-size: 1.2rem;
  font-weight: 500;
}
.main_item_title{
  font-size: 1.4rem;
	font-weight: 600;
  text-align: left;
	color: #192E49;
	text-overflow: ellipsis;
  padding-bottom: 0;
	width:100%;
  padding: 1.2rem;
}
.main_item_text{
  font-size: 1rem;
	font-weight: 500;
  color: #909090;
  text-align: left;
	text-overflow: ellipsis;
	word-wrap:break-word;
	width:100%;
}
.main_date{
  font-size: 1rem;
}
.icon_img{
  margin-top: 1.5rem;
  width: 100px !important; 
}
.main_item_img{
  height: 250px;
  width: 100%;
  -webkit-transform:scale(1);
	-moz-transform:scale(1);
	-ms-transform:scale(1);
	-o-transform:scale(1);
	transform:scale(1);
  -webkit-transition:.5s;
	-moz-transition:.5s;
	-ms-transition:.5s;
	-o-transition:.5s;
	transition:.5s;
}
.img1_box{
  margin-top: 0;
  margin-bottom: -5rem;
}
.shadow{
  z-index: 1;
  top: -130px;
  left: -15px;
  animation: cloud_shadow-in 5s forwards infinite;
}
.img1{
  cursor:pointer;
  z-index: 2;
  animation: cloud-in 5s forwards infinite;
}
.img2{
  transform: scale(0.85);
  cursor:pointer;
}
.img3{
  cursor:pointer;
}
.img2:hover{
  animation: shake-in 5s forwards infinite;
}
.img3:hover{
  animation: rotate-in 5s forwards infinite;
}
.main_card{
  background-color: transparent !important;
}
.main_card_item{
  padding: 0 5rem;
}
.main_text_group{
  padding-left: 5rem;
}
.main_text_group_right{
  padding-right: 5rem;
}
.main_card:hover{
  animation: main_card-in 0.3s forwards alternate;
}
@keyframes main_card-in {
  from{
    transform:scale(1) translateY(0px)
  }
  to{
    transform:scale(1) translateY(-2px);
    box-shadow: 3px 3px 20px #aaa;
  }
}
@keyframes cloud-in {
  0%{
    transform:scale(0.85) translateY(0px)
  }
  50%{
    transform:scale(0.9) translateY(-15px);
  }
  100%{
    transform:scale(0.85) translateY(0px)
  }
}
@keyframes cloud_shadow-in {
  0%{
    transform:scale(1) translateY(0px);
    opacity: 1;
  }
  50%{
    transform:scale(1.1) translateY(3px);
    opacity: .6;
  }
  100%{
    transform:scale(1) translateY(0px);
    opacity: 1;
  }
}
@keyframes shake-in {
  0%{
    transform:scale(0.85) translate3d(0, 0, 0) translateX(0px);    
  }
  50%{
    transform:scale(0.75) translate3d(10px, -10px, 50px) translateX(30px);
  }
  100%{
    transform:scale(0.85) translate3d(0, 0, 0) translateX(0px); 
  }
}
@keyframes rotate-in {
  0%{
    transform:scale(1) rotateX(0deg);
  }
  50%{
    transform:scale(0.9) rotateX(10deg);    
  }
  100%{
    transform:scale(1) rotateX(0deg);
  }
}
.main_card:hover .main_item_title{
  color: #143b83 !important;
}
@media screen and (max-width: 1600px) {
  .profile{
    height: 350px;
  }
  .main_card_item{
    padding: 1.5rem;
  }
}
@media screen and (max-width: 1200px) {
  .main_card_item{
    padding: 1rem;
  }
  .main_title{
    font-size: 1.8rem;
  }
  .main_sub{
    font-size: 0.95rem;
  }
  .main_item_title{    
    font-size: 1.1rem;
  }
  .shadow{
    top: -100px;
  }
}
@media screen and (max-width: 768px) {
  .profile{
    height: 300px;
  }
  .main_item_title{
    font-size: 1rem;
    margin: 0.5 1rem;
    width:100%;
  }
  .main_item_text{
    font-size: 0.75rem;
    padding: 1.05rem;
  }
  .main_item{
    padding: 3rem;
  }
  .main_title{
    font-size: 1.3rem;
    letter-spacing: 0;
  }
  .main_sub{
    font-size: 0.85rem;
  }
  .icon_img{
    width: 80px !important; 
  }
  .img1_box{
    margin-top: -2rem;
    margin-bottom: -4rem;
  }
  .shadow{
    left: -20px;
    top: -70px;
  }
  .main_text_group{
    padding-left: 0;
  }
  .main_text_group_right{
    padding-right: 0rem;
  }
  .main_item_img{
    height: 212px;
  }
  .dark-text{
    background: linear-gradient(rgba( 255, 255, 255, 0.8 ),rgba( 255, 255, 255, 0.65 ),rgba( 255, 255, 255, 0 ));
  }
}
@media screen and (max-width: 600px) {
  .main_item_img{
    height: 180px;
  }
  .main_item_title{
    font-size: 0.85rem;
    padding: 0.5rem;
    height: 30px;
    width:100%;
    -webkit-line-clamp: 1; /* 라인수 */
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
  }
  .main_item_text{
    font-size: 0.65rem;
    height: 51px;
    padding: 0.5rem;
    -webkit-line-clamp: 2; /* 라인수 */
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
  }
  .shadow{
    top: -60px;
    left: -20px;
  }
}
@media screen and (max-width: 400px) {
  .main_item_title{
    padding: 0.5rem 0;
    margin-bottom: 0.5rem;
    height: 30px;
  }
  .main_item_text{
    height: 50px;
    padding: 0rem;
  }
}
</style>
